import { MDBAnimation } from "mdbootstrap";
import React, { useEffect, useState } from "react";
import { Typography } from "./base/typography";

const start = new Date("04/2/2022 2:00 PM GMT-0400").getTime();
const finish = new Date("04/3/2022 2:00 PM GMT-0400").getTime();

const formatTime = (timeMs: number) => {
	if (timeMs == -1) return "--:--:--";
	const time = Math.floor(timeMs / 1000);
	const hours = Math.floor(time / 3600);
	const minutes = Math.floor((time % 3600) / 60);
	const seconds = time % 60;
	return `${hours.toString().padStart(2, "0")}:${minutes.toString().padStart(2, "0")}:${seconds
		.toString()
		.padStart(2, "0")}`;
};

async function delay(ms: number) {
	return new Promise((resolve) => setTimeout(resolve, ms));
}

const Countdown: React.FC = () => {
	const [state, setState] = useState("Starting in");
	const [time, setTime] = useState(start - new Date().getTime());
	const [clickAnimation, setClickAnimation] = useState(false);

	useEffect(() => {
		const interval = setInterval(async () => {
			const current = new Date().getTime();
			const startDelta = start - current;
			if (startDelta > 0) {
				setState("Starting in");
				setTime(startDelta);
				setClickAnimation(true);
				await delay(250);
				setClickAnimation(false);
			} else {
				const finishDelta = finish - current;
				if (finishDelta > 0) {
					setTime(finishDelta);
					setState("Closing in");
					setClickAnimation(true);
					await delay(250);
					setClickAnimation(false);
				} else {
					setState("Ended");
					setTime(-1);
					clearInterval(interval);
				}
			}
		}, 1000);
		return () => clearInterval(interval);
	}, [time]);
	return (
		<div>
			<Typography style={{ fontSize: "2em", paddingTop: "0.5em" }}>{state}</Typography>
			<MDBAnimation
				reset
				enableTarget
				target={clickAnimation}
				setTarget={setClickAnimation}
				tag="div"
				animation="pulse"
				start="onClick"
				duration={250}
			>
				<Typography style={{ fontSize: "5em", padding: "0", lineHeight: 0.9 }}>
					<b>{state === "Ended" ? "--:--:--" : <u>{formatTime(time)}</u>}</b>
				</Typography>
			</MDBAnimation>
		</div>
	);
};

export default Countdown;
